* {
    box-sizing: border-box;
    text-decoration: none;
    margin: 0;
    padding: 0;
  }
  
  body {
    font-family: "DM Sans", sans-serif;
    font-family: "Titan One", cursive;
  }
  
  header {
    height: 664px;
    background: linear-gradient(145deg, #FFB8CA, #7c1d3d);
    background-size: 300% 200%;
    position: relative;
    -webkit-animation: BgGradient 6s ease infinite;
            animation: BgGradient 6s ease infinite;
  }
  
  @-webkit-keyframes BgGradient {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  @keyframes BgGradient {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  .nav {
    display: flex;
    justify-content: space-around;
  }
  .nav__elipse {
    position: absolute;
    margin-top: 75px;
    margin-left: 360px;
  }
  .nav__img {
    margin-top: 37px;
  }
  .nav__link--pink2 {
    margin-top: 54px;
    color: #D41443;
  }
  .nav__link {
    margin-top: 54px;
    color: #FFFFFF;
    justify-content: space-around;
  }
  .nav__link:hover {
    color: #D41443;
  }
  .nav__link--2 {
    margin-top: 54px;
  }
  .nav__button {
    width: 159px;
    height: 44px;
    border: none;
    border-radius: 22px;
    background-color: #FFFFFF;
    text-align: left;
    margin-top: 40px;
  }
  .nav__button:hover {
    transform: translateY(-10px);
  }
  .nav__span {
    font-family: "DM Sans", sans-serif;
    font-size: 16px;
    line-height: 21px;
    font-weight: 700;
    margin-left: 37px;
    color: #D41443;
  }
  .nav__svg {
    margin-left: 12px;
  }
  
  .ice {
    position: absolute;
    margin-left: 750px;
    margin-top: 17px;
  }
  
  .content {
    float: left;
    position: relative;
    margin-top: 74px;
    margin-left: 70px;
  }
  .content__title {
    font-family: "Titan One", cursive;
    font-size: 38px;
    line-height: 43.51px;
    letter-spacing: 4%;
    color: #FFFFFF;
  }
  .content__title--2 {
    font-family: "Titan One", cursive;
    font-size: 38px;
    line-height: 43.51px;
    letter-spacing: 4%;
    color: #D41443;
  }
  .content__buttons {
    margin-top: 24px;
  }
  .content__button {
    width: 107px;
    height: 40px;
    border: none;
    border-radius: 20px;
    background-color: #D41443;
    font-family: "DM Sans", sans-serif;
    font-size: 14px;
    line-height: 18px;
    font-weight: 400;
    color: #FFFFFF;
    margin-right: 12px;
  }
  .content__button:hover {
    transform: translateY(-5px);
  }
  .content__button--whithe {
    width: 138px;
    height: 40px;
    border: none;
    border-radius: 20px;
    background-color: #FFFFFF;
    font-family: "DM Sans", sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    color: #D41443;
  }
  .content__button--whithe:hover {
    transform: translateY(-5px);
  }
  
  .group {
    margin-left: 1280px;
  }
  .group__svg {
    margin-top: 66px;
  }
  
  .group-1 {
    position: absolute;
  }
  .group-1__numbers {
    position: absolute;
  }
  .group-1__numbers {
    font-family: "Titan One", cursive;
    color: #D41443;
    font-size: 46px;
    margin-top: 66px;
    margin-left: 1248px;
  }
  .group-1__numbers--23 {
    position: absolute;
    font-family: "Titan One", cursive;
    color: #D41443;
    font-size: 46px;
    margin-top: 36px;
    margin-left: 1240px;
  }
  .group-1__text {
    font-family: "DM Sans", sans-serif;
    font-size: 14px;
    line-height: 18px;
    margin-top: 112px;
    margin-left: 1248px;
    color: #FFFFFF;
  }
  .group-1__text--truck {
    font-family: "DM Sans", sans-serif;
    font-size: 14px;
    line-height: 18px;
    margin-top: 86px;
    margin-left: 1230px;
    color: #FFFFFF;
  }
  
  .group-2 {
    clear: both;
    margin-left: 70px;
  }
  .group-2__img {
    float: left;
    margin-top: 40px;
  }
  .group-2__p {
    float: left;
    margin-top: 110px;
    color: #FFFFFF;
    font-family: "DM Sans", sans-serif;
    font-size: 14px;
    line-height: 20px;
  }
  .group-2__svg {
    margin-top: 20px;
  }
  .group-2__svg:hover {
    margin-left: 2px;
    scale: 1.5;
    transform: rotate(90deg);
  }
  
  /* bubbles */
  .burbujas {
    opacity: 0.4;
  }
  .burbujas__burbuja {
    bottom: 0;
    border-radius: 50%;
    position: absolute;
    -webkit-animation: bubbles normal infinite;
            animation: bubbles normal infinite;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
  }
  .burbujas__burbuja:nth-child(1) {
    width: 40px;
    height: 40px;
    left: 1%;
    -webkit-animation-duration: 9.1s;
            animation-duration: 9.1s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(2) {
    width: 80px;
    height: 80px;
    left: 3%;
    -webkit-animation-duration: 18s;
            animation-duration: 18s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(3) {
    width: 20px;
    height: 20px;
    left: 8%;
    -webkit-animation-duration: 6s;
            animation-duration: 6s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(4) {
    width: 50px;
    height: 50px;
    left: 10%;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(5) {
    width: 70px;
    height: 70px;
    left: 20%;
    -webkit-animation-duration: 9s;
            animation-duration: 9s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(6) {
    width: 100px;
    height: 100px;
    left: 28%;
    -webkit-animation-duration: 11s;
            animation-duration: 11s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(7) {
    width: 60px;
    height: 60px;
    left: 35%;
    -webkit-animation-duration: 9.2s;
            animation-duration: 9.2s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(8) {
    width: 30px;
    height: 30px;
    left: 40%;
    -webkit-animation-duration: 5.2s;
            animation-duration: 5.2s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(9) {
    width: 200px;
    height: 200px;
    left: 42%;
    -webkit-animation-duration: 7s;
            animation-duration: 7s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(10) {
    width: 100px;
    height: 100px;
    left: 50%;
    -webkit-animation-duration: 10s;
            animation-duration: 10s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(11) {
    width: 30px;
    height: 30px;
    left: 55%;
    -webkit-animation-duration: 6s;
            animation-duration: 6s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(12) {
    width: 60px;
    height: 60px;
    left: 60%;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(13) {
    width: 20px;
    height: 20px;
    left: 69%;
    -webkit-animation-duration: 9s;
            animation-duration: 9s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(14) {
    width: 100px;
    height: 100px;
    left: 72%;
    -webkit-animation-duration: 11s;
            animation-duration: 11s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(15) {
    width: 60px;
    height: 60px;
    left: 80%;
    -webkit-animation-duration: 9s;
            animation-duration: 9s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(16) {
    width: 20px;
    height: 20px;
    left: 88%;
    -webkit-animation-duration: 14s;
            animation-duration: 14s;
    background-color: #FFFFFF;
  }
  .burbujas__burbuja:nth-child(17) {
    width: 100px;
    height: 100px;
    left: 90%;
    -webkit-animation-duration: 7s;
            animation-duration: 7s;
    background-color: #FFFFFF;
  }
  @-webkit-keyframes bubbles {
    5% {
      margin-left: 8px;
      transform: translateX(4px);
    }
    10% {
      transform: translateX(30px);
    }
    25% {
      margin-right: 0px;
      transform: translateX(-90px);
    }
    100% {
      bottom: 100vh;
    }
  }
  @keyframes bubbles {
    5% {
      margin-left: 8px;
      transform: translateX(4px);
    }
    10% {
      transform: translateX(30px);
    }
    25% {
      margin-right: 0px;
      transform: translateX(-90px);
    }
    100% {
      bottom: 100vh;
    }
  }
  
  @media only screen and (max-width: 600px) {
    .nav {
      background-color: green;
    }
    .nav__elipse {
      margin-top: 60px;
    }
}